.slider {
  position: relative;
  overflow: hidden; }
  .slider__wrapper {
    display: inline-flex;
    flex-direction: row;
    flex-wrap: nowrap; }
    .slider__wrapper:not(.isMoving) {
      transition: transform .3s; }
  .slider__dots {
    position: absolute;
    bottom: .8rem;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    width: 100%; }
    .slider__dots__item {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      position: relative; }
      .slider__dots__item:not(:last-child) {
        margin-right: 1rem; }
      .slider__dots__item .wrapper {
        width: 1.2rem;
        height: 1.2rem;
        position: absolute;
        background-color: transparent;
        border-radius: 50%;
        border: 1px solid transparent;
        transition: transform .3s;
        transform: scale(0) translate(-25%, -25%);
        top: 0;
        left: 0; }
        .slider__dots__item .wrapper.active {
          border-color: #fff;
          transform: scale(1) translate(-25%, -25%); }
      .slider__dots__item span {
        background-color: #fff;
        width: .6rem;
        height: .6rem;
        border-radius: 50%; }
