.butter-menu {
  position: absolute;
  display: flex;
  align-items: center;
  z-index: 2; }
  .butter-menu.sign .f, .butter-menu.sign .s, .butter-menu.sign .t {
    background-color: #ffffff; }
  .butter-menu .bars {
    width: 30px;
    height: 17px;
    flex-direction: column;
    justify-content: center;
    cursor: pointer;
    display: inline-flex; }
    .butter-menu .bars div {
      transition: all .5s;
      width: 100%;
      height: 2px; }
      .butter-menu .bars div:not(:last-child) {
        margin-bottom: .35rem; }
      .butter-menu .bars div.appear {
        transform: translateX(0) rotate(0deg); }
  .butter-menu .f, .butter-menu .s, .butter-menu .t {
    background-color: #ffffff; }
  .butter-menu .f {
    transform: translateX(-1000%) rotate(0deg) translate(0, 0);
    transform-origin: 100% 0; }
  .butter-menu .s {
    transform: translateX(-1000%); }
  .butter-menu .t {
    transform: translateX(-1000%);
    opacity: 1; }
  .butter-menu .side_menu {
    overflow: hidden;
    width: 0px;
    position: absolute;
    right: 0;
    top: 55px;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.8);
    transition: width .3s; }
  .butter-menu.open .s {
    transform: rotate(45deg) !important; }
  .butter-menu.open .t {
    opacity: 0; }
  .butter-menu.open .f {
    transform-origin: 100% 0 !important;
    transform: rotate(-45deg) translate(-2px, -6px) !important; }
  .butter-menu.right .f {
    transform: translateX(1000%) rotate(0deg) translate(0, 0); }
    .butter-menu.right .f.appear {
      transform: translateX(0) rotate(0deg); }
  .butter-menu.right .s {
    transform: translateX(1000%); }
    .butter-menu.right .s.appear {
      transform: translateX(0) rotate(0deg); }
  .butter-menu.right .t {
    transform: translateX(1000%); }
    .butter-menu.right .t.appear {
      transform: translateX(0) rotate(0deg); }
  .butter-menu.right .side_menu li {
    transform: translateX(200%); }
