.menu-item {
  text-transform: uppercase;
  margin-right: 1.5rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  position: relative;
  transition: color .3s;
  font-size: .9rem;
  font-weight: 500;
  user-select: none;
  color: #ffffff; }
  .menu-item:hover {
    color: #FF8B00; }
  .menu-item .sub-menu {
    display: none;
    opacity: 0;
    transform: translateY(-10%);
    transition: transform .5s, opacity .5s; }
  .menu-item svg {
    transition: transform .3s;
    margin-left: .8rem; }
  .menu-item.open .sub-menu {
    display: flex;
    opacity: 1;
    transform: translateY(0%); }
  .menu-item.open svg {
    transform: rotate(180deg); }
