.menu-banner {
  position: relative;
  top: 0;
  left: -2rem;
  min-width: calc(100% + 2rem * 2);
  display: flex;
  font-size: 1rem; }
  @media (max-width: 63.999em) {
    .menu-banner {
      position: relative;
      font-size: .7rem;
      left: -1rem;
      min-width: calc(100% + 1rem * 2); } }
  .menu-banner__item {
    width: 25%;
    position: relative;
    display: flex; }
    @media (max-width: 63.999em) {
      .menu-banner__item {
        width: 100vw !important;
        height: 13rem; } }
    .menu-banner__item:after {
      content: '';
      transition: background-color .3s;
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.3);
      left: 0;
      top: 0; }
      .menu-banner__item:after:hover .menu-banner__item:after {
        background-color: rgba(179, 0, 0, 0.35); }
    .menu-banner__item.wide {
      width: 50%; }
      .menu-banner__item.wide h3 {
        font-size: 3em; }
      .menu-banner__item.wide .description {
        font-size: 1.3em; }
      .menu-banner__item.wide .author {
        letter-spacing: 3px; }
  .menu-banner__content {
    position: absolute;
    bottom: 1rem;
    left: 3rem;
    width: calc(100% - 6rem);
    color: #ffffff;
    z-index: 2; }
    @media (max-width: 63.999em) {
      .menu-banner__content {
        left: 2rem; } }
  .menu-banner h3 {
    font-size: 1.8em; }
  .menu-banner .author {
    letter-spacing: 3px; }
