.menu-mobile {
  position: relative; }
  .menu-mobile .search {
    position: absolute;
    top: 0;
    right: 0; }
    .menu-mobile .search svg {
      width: 1.3rem;
      height: 1.3rem; }

.butter-menu {
  margin-top: 1rem; }
  .butter-menu.open .main-menu-mobile {
    transform: translateX(0); }
  .butter-menu .main-menu-mobile {
    position: absolute;
    top: -1rem;
    left: -1rem;
    background-color: #ffffff;
    transition: transform .3s;
    transform: translateX(-150%);
    width: 100vw;
    min-height: 100vh;
    padding: 2rem 0; }
    .butter-menu .main-menu-mobile .close {
      position: absolute;
      top: 1rem;
      right: 1rem; }
    .butter-menu .main-menu-mobile .sub-menu {
      padding: 1rem;
      font-size: 1rem; }
      @media (max-width: 63.999em) {
        .butter-menu .main-menu-mobile .sub-menu {
          font-size: .9em; }
          .butter-menu .main-menu-mobile .sub-menu .sub-menu__content {
            flex-wrap: wrap; } }
    .butter-menu .main-menu-mobile .menu-item {
      color: #282828;
      padding: 1rem;
      margin-right: 0; }
