.search {
  padding: 1rem; }
  .search:hover svg {
    color: #FF8B00; }
  .search svg {
    color: #ffffff;
    width: 1.5rem;
    height: 1.5rem;
    cursor: pointer; }
  .search .search-field {
    padding: 1rem;
    position: absolute;
    top: 3rem;
    right: 0;
    background-color: #ffffff;
    border-radius: 5px; }
    @media (max-width: 63.999em) {
      .search .search-field {
        padding: .5rem; } }
  .search .input input {
    padding: 1.3rem 1rem;
    font-size: 1.2em;
    min-width: 30rem; }
    @media (max-width: 63.999em) {
      .search .input input {
        font-size: .9em;
        padding: .5rem 1rem;
        min-width: 14rem; } }
  .search .input .hero-right {
    padding: 1.3rem 3rem;
    font-size: 1.2em; }
    @media (max-width: 63.999em) {
      .search .input .hero-right {
        font-size: .9em;
        padding: .5rem 1rem; } }
