.page-wrapper {
  min-height: 100vh;
  padding: 0 2rem; }
  @media (max-width: 63.999em) {
    .page-wrapper {
      padding: 0 1rem; } }

.page-banner {
  margin-right: -2rem;
  margin-left: -2rem; }
  @media (max-width: 63.999em) {
    .page-banner {
      margin-right: -1rem;
      margin-left: -1rem; } }
